<template>
    <v-app class="ma-0">
        <div id="login"></div>
        <v-layout
            align-center justify-center
            column fill-height
        >
            <v-card id="logbox" class="ma-auto" height="97vh" width="45vw"
                raised hover
            >
                <v-responsive class="text-xs-center " height="25%"
                >
                    <img src="@/assets/mote-logo.png"/>
                </v-responsive>
                <v-divider></v-divider>
            <v-form ref="form" class="mt-5">
                    <v-layout
                    align-center justify-center
                    column fill-height
                >
                    <v-text-field
                        v-model="name"
                        label="Name"
                        color="secondary"
                        outline
                        prepend-inner-icon="account_circle"
                        style="width:50%; border-radius: 10px;"
                    >

                    </v-text-field>

                     <v-text-field
                        v-model="email"
                        label="Email"
                        color="secondary"
                        outline
                        prepend-inner-icon="account_circle"
                        style="width:50%; border-radius: 10px;"
                    >
                    </v-text-field>

                    <v-text-field
                        v-model="password"
                        type="password"
                        label="Password"
                        color="primary"
                        outline
                        prepend-inner-icon="lock"
                        style="width:50%;"
                    >
                    </v-text-field>

                   <v-checkbox :value="isAdmin"
                    label="Register As Admin User?"
                   ></v-checkbox>

                    <br>

                    <v-btn ripple raised
                        color="primary" style="font-size: 12pt; width: 50%"
                        large
                        @click="signUp()"

                    >
                     Register
                    </v-btn>

                </v-layout>


                </v-form>
            </v-card>
        </v-layout>

    </v-app>

</template>

<script>
import Footer from '@/components/extras/Footer.vue';
import { error } from 'util';
import firebase from 'firebase';

export default {
  components: {
    Footer,
  },
  data: () => ({
    name: '',
    email: '',
    password: '',
    isAdmin: false,
  }),
  methods: {
    signUp() {
        firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
        .then(
            (user) => {
                console.log('Account has been created!');
            },
            (err) => {
                console.log(`Oh no! ${err.message}`);
            }
        );
    }
  }
};
</script>


<style>
    #login {
        background: url('../assets/bck.jpg');
        min-height: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        filter: blur(8px);
        -webkit-filter: blur(8px);
    }

    #logbox {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
    }
</style>
