<template>
    <v-app class="ma-0" id="log">
        <LoginBar />
        
        <v-layout
            align-center justify-center
            column fill-height
        >
            <v-card id="logbox" 
                class="ma-auto pa-4" 
                width="40vw" 
                max-height="1500pt"
                hover
                
            >
            <p class="mt-3 mb-1" style="font-size: 30pt;">
                Sign In
            </p>
            <v-form ref="form" class="mt-3 mb-0">
                    <v-layout
                    align-center justify-center
                    column fill-height
                >
                    <v-alert :value="error"
                        style="border-radius: 30px;"
                        class="mb-4"
                    >
                        Invalid credentials. Please try again or reset your password.
                    </v-alert>

                    <v-text-field
                        v-model="email"
                        outline
                        label="Email"
                        color="secondary"
                        append-icon="email"
                        class="input"
                        style="width:90%; border-radius: 50px;"
                    >

                    </v-text-field>

                    <v-spacer></v-spacer>

                    <v-text-field
                        v-model="password"
                        outline
                        type="password"
                        label="Password"
                        color="secondary"
                        append-icon="lock"
                        style="width:90%;"
                        class="input"
                    >
                    </v-text-field>

                    <v-btn ripple raised
                        color="primary" style="font-size: 12pt; width: 60%; border-radius: 40px;"
                        large
                        @click="login()"
                        class="mb-3 mt-1"
                        :loading="loading"
                        :disabled="loading"
                    >
                    <span>
                        Sign In
                    </span>
                     
                    </v-btn>
                    <v-btn flat
                        color="primary"
                        style="border-radius: 30px;"
                    >
                        Forgot Password?
                    </v-btn>
                </v-layout>


                </v-form>
            </v-card>
        </v-layout>

    </v-app>

</template>

<script>
import LoginBar from '@/components/extras/LoginBar.vue';
import { mapActions } from 'vuex';
import firebase from 'firebase';

export default {
  components: {
    LoginBar,
  },
  data: () => ({
    email: '',
    password: '',
    loading: false,
    error: false,
  }),

  methods: {
    login() {
        this.loading = true;
        firebase.auth().signInWithEmailAndPassword(this.email, this.password)
        .then(
            (user) => {
                //console.log('You are signed in!');
                this.$router.replace('/dashboard');
                this.loading = false;
            },
            (err) => {
                this.loading = false;
                this.error = true;
            }
        );
    },
  },

};
</script>


<style>
    #log {
        background: url('../assets/leaf.jpg');
        min-height: 100%;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    #logbox {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
        border-radius: 30px;
        text-align: center;
        font-family: 'Amiko', sans-serif;
        font-size: 30pt;
    }
</style>
